@import 'mixins';

/*
Plugins
---------------------------------------------------------------------------------------------------- */

/* Genesis eNews Extended
--------------------------------------------- */

.enews-widget {
  color: #999;
}

.enews-widget .widget-title {
  color: @white;
}

.enews-widget input {
  margin-bottom: 16px;
  margin-bottom: 1.6rem;
}

.enews-widget input:focus {
  border: 1px solid #ddd;
}

.enews-widget input[type="submit"] {
  background-color: @primaryGreen;
  color: @white;
  margin: 0;
  width: 100%;
}

.enews-widget input:hover[type="submit"] {
  background-color: @white;
  color: @dkgrey;
}

/* Genesis Latest Tweets
--------------------------------------------- */

.latest-tweets ul li {
  margin-bottom: 16px;
  margin-bottom: 1.6rem;
}

/* Gravity Forms
--------------------------------------------- */

.gform_wrapper {
  h3 {
    margin: 0;
  }

  ::-webkit-input-placeholder {
    color: #888888;
  }
  ::-moz-placeholder {
    color: #888888;
  }
  :-ms-input-placeholder {
    color: #888888;
  }
  :-moz-placeholder {
    color: #888888;
  }
}

div.gform_wrapper input[type="email"],
div.gform_wrapper input[type="text"],
div.gform_wrapper input[type="tel"],
div.gform_wrapper textarea,
div.gform_wrapper .ginput_complex label,
div.gform_wrapper select {
  padding: .25em .5em !important;
  font-size: 1.4rem !important;
  font-weight: normal;
}

div.gform_wrapper .ginput_complex label {
  padding: 0;
}

div.ginput_complex.ginput_container.gf_name_has_2 span {
  width: 50%;
  display: inline-block;
  margin-right: -4px;

  &:not(:last-child) {
    padding-right: 1rem;
  }
}

div.gform_wrapper ul {
  list-style: none;
  margin: 0;
}

div.gform_wrapper li,
div.gform_wrapper form li {
  margin: 0;
  padding: .75rem 0;
  list-style-type: none;

  @media @desktop {
    padding: .3rem 0;
  }
}

.gform_wrapper ul.gform_fields li.gfield {
  padding-right: 0;
}

div.gform_wrapper .gform_body .gfield{
  //padding: 0.5em;
  &.even{}
  &.odd{}
  .ginput_container{
    .gfield_radio{
      li{
        display: inline-block;
        min-width: 16%;
        input,label{
          text-align: center;
          display: block;
          margin: 1px auto;
          float: none;
          font-weight: 400;
        }
      }
    }
  }
}

div.gform_wrapper .gform_footer input[type="submit"] {
  font-size: 14px;
  font-size: 1.4rem;
  display: table;
}

form.gplaceholder {
  .gfield_label {
    display: none;
  }
}

/* Jetpack
--------------------------------------------- */

img#wpstats {
  display: none;
}

.gfield{
  table.gsurvey-likert{
    border: 0;
    tbody{
      border:0;
      tr{
        border: 0;
        td{
          border: 0;
          &.gsurvey-likert-choice-label{
            background: none;
          }
        }
      }
    }
  }
}
.entry-content{
ul.news-items,ul.press-items{
  list-style: none outside none;
  margin-left: 0;
  li{
    list-style: none outside none;
  }
}
ul.news-items div.page{
  display: none;
  &.active{
    display: block;
  }
}
ul.publication-list-pagination{
  list-style: none outside none;
  margin-left: 0;
  text-align: center;
  li{
    list-style: none outside none;
    display: inline-block;
    cursor: pointer;
    padding: 0 1em;
  }
}
}

.job-manager-application-wrapper .application,.single_job_listing .application{
  .application_button{
    .button();
    display: inherit;
    font-size: inherit;
    line-height: inherit;
    margin: inherit;
    outline: inherit;
    padding: inherit;
    text-align: inherit;
  }
}